.notification {
    padding: 10px;
    border-radius: 5px;
    margin: 8px;
}

.notification p{
    margin: 0;
}

.success {
    background-color: #3eff65;
    border-top: 5px solid #2ebf4b;
}

.error {
    background-color: #ff4640;
    border-top: 5px solid #bf3530;
}

.warning {
    background-color: #ffe852;
    border-top: 5px solid #bfae3d;
}

.hideme{
    display: none;
}

.showme{
    display: block;
}